import React, { Component } from 'react';
import { connect } from 'react-redux';
import _ from 'lodash';
import BookmarkList from './BookmarkList';
import { openCategory, clearCategory } from '../actions/bookmarks';

export class BookmarkCats extends Component {


    constructor(props) {
        super(props);

    }

    bookmarkCategories() {

        let bookmarks = this.props.bookmarks;

        let titles = _.keysIn(bookmarks);

        return titles.map((title) => {
            return (
              <li onClick={() => {this.openCat(title)}} key={title}>{title}</li>
            );
        })

    }

    openCat(cat) {
        this.props.openCategory(cat);
    }

    render() {

        if (this.props.opencategory) {
            return (
                <div>
                    <BookmarkList category={this.props.opencategory}/><br />
                    <a href="#" onClick={this.props.clearCategory}>Back to All Categories</a>
                </div>
            )
        } else if (!_.isEmpty(this.props.bookmarks)) {
            return (
                <div>
                    <h3>Choose a category:</h3>
                    <ul>
                        {this.bookmarkCategories()}
                    </ul>
                </div>
            );
        } else {
            //no bookmarks yet
            return <div><br />Loading...</div>
        }
    }
}


function mapStateToProps(state) {
    return {
        bookmarks: state.MarkyFile.bookmarks,
        opencategory: state.MarkyFile.openCat
    };
}

export default connect(mapStateToProps, {openCategory, clearCategory})(BookmarkCats);





