import * as constants from '../constants/AppConstants';
import * as types from '../constants/ActionTypes';

export function setupBookmarks(bookmarks) {
    return {
        type: types.SETUP_BOOKMARKS,
        payload: bookmarks
    }
}

export function openCategory(cat) {
    return {
        type: types.OPEN_CATEGORY,
        payload: cat
    }
}


export function clearCategory(cat) {
    return {
        type: types.CLEAR_CATEGORY,
        payload: cat
    }
}


export function createBookmark(category,name,url) {
    return {
        type: types.CREATE_BOOKMARK,
        payload: category

    }
}


