import React, { Component } from 'react';
import { connect } from 'react-redux';
import AddBookmark from './AddBookmark';

export class BookmarkList extends Component {

    constructor(props) {
        super(props);
    }


    displayCat(title) {
        return <h3>{title}</h3>
    }

    displayList() {

        let items = this.props.bookmarks[this.props.category];

        return items.map((bookmark, i) => {
            console.log(i);
            let catName = this.props.category;
            let itemName = bookmark[0];
            let itemURL = bookmark[1];

            if (i > 0) {
                catName = '';
            }

            return (
                <div>
                {this.displayCat(catName)}
                    <li>
                        <a href={itemURL} target="_blank">{itemName}</a>
                    </li>
                </div>
            )
        });

    }

    render() {

        return (
        <div>
            {this.displayList()}
            <AddBookmark category={this.props.category} />
        </div>
        );
    }
}


function mapStateToProps(state) {
    return {
        bookmarks: state.MarkyFile.bookmarks
    };
}

export default connect(mapStateToProps, null)(BookmarkList);





