import React, { Component } from 'react';
import { connect } from 'react-redux';
import { logoutUser } from '../actions/driveAccess';

export class UserMenu extends Component {
    render() {
        if(this.props.id) {
            return (
                <div>
                    <img src={this.props.avatar} className="user-profile-img" />
                    {this.props.name} <button onClick={this.props.logoutUser}>Sign Out</button>
                </div>
            )
        } else {
            return (
                <div></div>
            )
        }
    }
}


function mapStateToProps(state) {
    return {
        id: state.userInfo.id,
        name: state.userInfo.name,
        avatar: state.userInfo.avatar,
    };
}


export default connect(mapStateToProps, {logoutUser})(UserMenu);



