import * as constants from '../constants/AppConstants';
import * as types from '../constants/ActionTypes';

export function MarkyFile(state = constants.initialBookmarks, action) {
    switch (action.type) {

        case types.SETUP_BOOKMARKS:
            return Object.assign({}, state, action.payload);

        case types.RESET_MARKY_FILE:
            return Object.assign({}, state, action.payload);

        case types.OPEN_CATEGORY:
            return Object.assign({}, state, { openCat: action.payload});

        case types.CREATE_BOOKMARK:
            console.log(action.payload);
            return Object.assign({}, state);

        case types.CLEAR_CATEGORY:
            return Object.assign({}, state, { openCat: null });

        default:
            return state
    }
}




